@font-face {
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 200;
  font-display: swap;
  src: local('Montserrat ExtraLight'), local('Montserrat-ExtraLight'),
    url(https://fonts.gstatic.com/s/montserrat/v14/JTURjIg1_i6t8kCHKm45_aZA3gTD_vx3rCubqg.woff2)
      format('woff2');
  unicode-range: U+0460-052F, U+1C80-1C88, U+20B4, U+2DE0-2DFF, U+A640-A69F,
    U+FE2E-FE2F;
}
.btn-action,
.btn-action button {
  border-radius: 0;
  border-width: 1px;
  letter-spacing: 1px;
  width: 100%;
  font-weight: normal;
  font-size: 0.9em;
  margin-top: 10px;
  text-transform: uppercase;
  line-height: 30px;
}
